import React, { useEffect, useState } from "react"
import { useSelector, shallowEqual, useDispatch } from "react-redux"
import encodeConverter from "../../../../functions/htmldecoder"
import { useLocation } from "@reach/router"
import Grid from "@material-ui/core/Grid"

import HelmetSeo from "../../../../shared/components/AC-Helmet/HelmetSeo"

import {
  fetchCategoryFromDirectUrl,
  backToCategoryNormalize,
} from "../../../../redux/actions/categoryActions"

import { PROJECT_LINK } from "../../../../project-config"
import { usePrevious } from "../../../../functions/Utilities.js"

import classes from "../CategoryHeader/Styles/BDCategoryHeader.module.css"

// const CategoryBreadcrumb = () => (
//   <Async load={import("../../../AC-Breadcrumb/BDCategoryBreadcrumb.jsx")} />
// );

export default function CategoryHeader() {
  const location = useLocation()
  const dispatch = useDispatch()
  const [categoryNameRender, setCategoryNameRender] = useState("")

  const prevPathName = usePrevious(location.pathname)

  const navCats = useSelector(
    state => state.menuReducer.navCategory,
    shallowEqual
  )

  const categoryNameState = useSelector(
    state => state.categoryReducer.cat,
    shallowEqual
  )

  const categoryImageState = useSelector(
    state => state.categoryReducer.catImage,
    shallowEqual
  )

  const filteredCatImage = categoryImageState
    ? categoryImageState.split(" ").join("%20")
    : categoryImageState

  const cidN = useSelector(state => state.categoryReducer.cidN, shallowEqual)

  /*Checking Category Name with URL Location if not match fetch the data again
  This funtion is for back button helper*/
  useEffect(() => {
    let prevPath = prevPathName
    dispatch(fetchCategoryFromDirectUrl())
    console.info(
      "langCode -",
      `url:'${location.pathname}'`,
      `prevPath:'${prevPath}'`,
      prevPath !== location.pathname && prevPath !== undefined
    )
    return () => {
      console.info(
        "langCode +",
        `url:'${location.pathname}'`,
        `prevPath:'${prevPath}'`,
        `prevPath2:'${prevPathName}'`
      )
    }
  }, [location.pathname])

  useEffect(() => {
    if (categoryNameState) {
      console.info("MON---", categoryNameState, categoryNameRender)
      let nameState = categoryNameState
      if (categoryNameState.includes("&aacute;")) {
        nameState = categoryNameState.replace("&aacute;", "á")
      }
      if (categoryNameState.includes("&eacute;")) {
        nameState = categoryNameState.replace("&eacute;", "é")
      }
      if (categoryNameState.includes("&iacute;")) {
        nameState = categoryNameState.replace("&iacute;", "í")
      }
      setCategoryNameRender(nameState)
    }
  }, [categoryNameState])

  return (
    <React.Fragment>
      <HelmetSeo
        title={categoryNameRender}
        desc={categoryNameRender}
        cid={cidN}
      />
      <div
        className={
          window.location.pathname.includes("/search")
            ? classes.subNavWrapper2
            : classes.subNavWrapper
        }
        style={{
          background: window.location.pathname.includes("search/")
            ? null
            : `url(${PROJECT_LINK}/store${filteredCatImage})`,
        }}
      >
        <div className={classes.catInfo}>
          <div className="sub-nav-title-desc-wrapper">
            <Grid container justify="space-between" alignItems="center">
              <Grid item xs={12}>
                <h1
                  className={classes.catName}
                  dangerouslySetInnerHTML={{ __html: categoryNameRender }}
                ></h1>
                {window.location.pathname.includes("/search") ? (
                  <div className={classes.catDesc}>Search Results</div>
                ) : null}
              </Grid>
              <Grid item xs={12}>
                <div
                  className={classes.catDesc}
                  style={{
                    display: navCats.longdesc != "" ? `flex` : `none`,
                  }}
                >
                  <div
                    style={{
                      margin: "0",
                    }}
                    dangerouslySetInnerHTML={{
                      __html: encodeConverter(navCats.longdesc),
                    }}
                  ></div>
                </div>
              </Grid>
            </Grid>
          </div>
        </div>
        {/* <CategoryBreadcrumb /> */}
      </div>
    </React.Fragment>
  )
}
